.button {
  display: flex;
  justify-content: center;
  align-items: center;

  box-sizing: border-box;
  width: 100%;
  height: 56px;

  color: $mainDarkColor;
  font-weight: 700;
  font-size: 16px;
  line-height: 27px;
  background-color: $mainColor;
  border-radius: 40px;
  cursor: pointer;

  transition: all 0.3s;

  &:hover {
    box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.4);
  }

  &:active {
    box-shadow: 1px 4px 16px 0 rgba(232, 232, 240, 0.64);
  }
}
